const frenchMonths = [
  "Janvier",
  "Fvrier",
  "Mars",
  "Avril",
  "Mai",
  "Juin",
  "Juillet",
  "Aot",
  "Septembre",
  "Octobre",
  "Novembre",
  "Dcembre",
];

export const lastNmonths = (n: number): string[] => {
  const currentMonth = new Date().getMonth();
  const months = [];

  for (let i = 0; i < n; ++i) {
    months.push(frenchMonths.at(currentMonth - i) ?? "?");
  }
  return months;
};
